<template>
  <div>
    <Breadcrumb text="" />

    <el-card>
      <el-button type="primary" icon="el-icon-plus" @click="addUser"></el-button>
      <el-table :data="adminUserList" border stripe>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="level"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small" type="primary" icon="el-icon-edit" @click="edit(scope.row)"></el-button>
            <el-button
              size="small"
              type="danger"
              icon="el-icon-delete"
              @click="_delete(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <el-dialog
      :title="!this.model._id?'':''"
      :visible.sync="dialogVisible"
      width="50%"
      @closed="dialogClose"
    >
      <el-form label-width="80px">
        <el-form-item label="">
          <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item v-if="!this.model._id" label="">
          <el-input v-model="model.level"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="model.password"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="ok"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
 
<script>
import Breadcrumb from '../../components/commom/Breadcrumb'

//
import { deleteAdminUser, getAdminUserList, createAdminUser, updateAdminUser, getAdminUser } from 'network/admin/adminUser'

export default {
  name: "AdminUserList",
  data() {
    return {
      //
      adminUserList: [],
      //
      model: {},
      dialogVisible: false
    }
  },
  components: {
    Breadcrumb
  },
  methods: {
    //
    async getAdminUserList() {
      const res = await getAdminUserList()
      this.adminUserList = res.data
    },
    addUser() {
      this.dialogVisible = true
    },
    //
    async edit(row) {
      this.id = row._id
      const res = await getAdminUser(this.id)
      this.model = res.data
      this.dialogVisible = true
    },
    async ok() {
      if (!this.model._id) {
        const res = await createAdminUser(this.model)
        if (!res) return
        this.$message.success('')
        this.getAdminUserList()
        this.dialogVisible = false
      } else {
        const res = await updateAdminUser(this.id, this.model)
        if (!res) return
        this.$message.success('')
        this.getAdminUserList()
        this.dialogVisible = false
      }
    },
    //
    async _delete(row) {
      try {

        await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })

        const res = await deleteAdminUser(row._id)
        this.$message.success(res.data.message)
        this.getAdminUserList()

      } catch (error) {
        this.$message.info('')
      }

    },
    dialogClose() {
      this.model = {}
    }
  },
  created() {
    this.getAdminUserList()
  }
}
</script>
 
<style scoped>
</style>