import { GUID_req } from '../request'

//
function createAdminUser(data) {
  return GUID_req({
    url: '/admin_users',
    method: 'post',
    data
  })
}

//id()
function getAdminUser(itemId) {
  return GUID_req({
    url: `/admin_users/${itemId}`
  })
}

//
function updateAdminUser(itemId, data) {
  return GUID_req({
    url: `/admin_users/${itemId}`,
    method: 'put',
    data
  })
}

//
function deleteAdminUser(itemId) {
  return GUID_req({
    url: `/admin_users/${itemId}`,
    method: 'delete'
  })
}

//
function getAdminUserList() {
  return GUID_req({
    url: '/admin_users'
  })
}

export {
  createAdminUser,
  getAdminUser,
  updateAdminUser,
  deleteAdminUser,
  getAdminUserList
}