import { GUID_req, upload } from '../request'

//
function createHero(data) {
  return GUID_req({
    url: '/heros',
    method: 'post',
    data
  })
}

//id()
function getHero(heroId) {
  return GUID_req({
    url: `/heros/${heroId}`
  })
}

//
function updateHero(heroId, data) {
  return GUID_req({
    url: `/heros/${heroId}`,
    method: 'put',
    data
  })
}

//
function deleteHero(heroId) {
  return GUID_req({
    url: `/heros/${heroId}`,
    method: 'delete'
  })
}

//
function getHeroList(params) {
  return GUID_req({
    url: '/heros',
    params
  })
}

export {
  createHero,
  getHero,
  updateHero,
  deleteHero,
  getHeroList
}