import { GUID_req, upload } from '../request'

//
function createAd(data) {
  return GUID_req({
    url: '/ads',
    method: 'post',
    data
  })
}

//id()
function getAd(itemId) {
  return GUID_req({
    url: `/ads/${itemId}`
  })
}

//
function updateAd(itemId, data) {
  return GUID_req({
    url: `/ads/${itemId}`,
    method: 'put',
    data
  })
}

//
function deleteAd(itemId) {
  return GUID_req({
    url: `/ads/${itemId}`,
    method: 'delete'
  })
}

//
function getAdList() {
  return GUID_req({
    url: '/ads'
  })
}

export {
  createAd,
  getAd,
  updateAd,
  deleteAd,
  getAdList
}