<template>
  <div>
    <breadcrumb :text="id?'':''" />
    <el-card>
      <el-form label-width="100px">
        <el-tabs>
          <el-tab-pane label="">
            <!--  -->
            <el-form-item label="">
              <el-input v-model="model.name"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-input v-model="model.title"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-upload
                class="avatar-uploader"
                :action="action+'hero'"
                :headers="token"
                :show-file-list="false"
                :on-success="res=>$set(model,'avatar',res.url)"
                :on-error="uploadErrHandler"
              >
                <img v-if="model.avatar" :src="model.avatar" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-upload
                class="avatar-uploader"
                :action="action+'hero'"
                :headers="token"
                :show-file-list="false"
                :on-success="res=>$set(model, 'banner', res.url)"
                :on-error="uploadErrHandler"
              >
                <img v-if="model.banner" :src="model.banner" class="banner" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <!--  -->
            <el-form-item label="" v-if="heroCateList">
              <el-select v-model="model.cate" placeholder="" multiple>
                <el-option
                  v-for="item in heroCateList"
                  :label="item.name"
                  :key="item._id"
                  :value="item._id"
                ></el-option>
              </el-select>
            </el-form-item>

            <!--  -->
            <el-form-item label="">
              <el-rate show-score :max="10" v-model="model.scores.difficulty"></el-rate>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-rate show-score :max="10" v-model="model.scores.skill"></el-rate>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-rate show-score :max="10" v-model="model.scores.attack"></el-rate>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-rate show-score :max="10" v-model="model.scores.survive"></el-rate>
            </el-form-item>
          </el-tab-pane>

          <el-tab-pane label="">
            <!--  -->
            <el-form-item label="">
              <el-select
                filterable
                v-model="model.downWind.equipment"
                placeholder=""
                multiple
              >
                <el-option
                  v-for="item in itemList"
                  :key="item._id"
                  :label="item.name"
                  :value="item._id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="model.downWind.tips" type="textarea"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-select filterable v-model="model.upWind.equipment" placeholder="" multiple>
                <el-option
                  v-for="item in itemList"
                  :key="item._id"
                  :label="item.name"
                  :value="item._id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="model.upWind.tips" type="textarea"></el-input>
            </el-form-item>
          </el-tab-pane>

          <el-tab-pane label="">
            <!--  -->
            <el-form-item label="">
              <el-input v-model="model.usageTips" type="textarea"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-input v-model="model.battleTips" type="textarea"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-input v-model="model.teamTips" type="textarea"></el-input>
            </el-form-item>
          </el-tab-pane>

          <el-tab-pane label="">
            <el-button type="primary" size="small" @click="addSkills">+ </el-button>

            <el-row :gutter="20" type="flex" style="flex-wrap: wrap">
              <el-col v-for="(item,i) in model.skills" :key="i" :span="12">
                <el-card>
                  <el-tag>{{i+1}}</el-tag>
                  <el-form-item label="" label-width="80px">
                    <el-input v-model="item.name"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="80px">
                    <el-upload
                      class="avatar-uploader"
                      :action="action+'skill'"
                      :headers="token"
                      :show-file-list="false"
                      :on-success="res => $set(item, 'icon', res.url)"
                      :on-error="uploadErrHandler"
                    >
                      <img v-if="item.icon" :src="item.icon" class="avatar" />
                      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                  </el-form-item>
                  <el-form-item label="" label-width="80px">
                    <el-input type="textarea" v-model="item.delay"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="80px">
                    <el-input type="textarea" v-model="item.cost"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="80px">
                    <el-input type="textarea" v-model="item.desc"></el-input>
                  </el-form-item>
                  <el-form-item label="" label-width="80px">
                    <el-input type="textarea" v-model="item.tips"></el-input>
                  </el-form-item>
                  <el-button type="danger" @click="deleteSkill(i)"></el-button>
                </el-card>
              </el-col>
            </el-row>
          </el-tab-pane>

          <el-tab-pane label="">
            <el-button type="primary" size="small" @click="model.partners.push({})">+ </el-button>
            <el-row :gutter="20" type="flex" style="flex-wrap: wrap">
              <el-col v-for="(item,i) in model.partners" :key="i" :span="12">
                <el-card>
                  <el-tag>{{i+1}}</el-tag>
                  <el-form-item label="" label-width="80px">
                    <el-select v-model="item.hero" filterable placeholder="">
                      <el-option
                        v-for="item in heroList"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="" label-width="80px">
                    <el-input type="textarea" v-model="item.desc"></el-input>
                  </el-form-item>
                  <el-button type="danger" @click="delPartner(i)"></el-button>
                </el-card>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane label="">
            <el-button type="primary" size="small" @click="model.restrained.push({})">+ </el-button>
            <el-row :gutter="20" type="flex" style="flex-wrap: wrap">
              <el-col v-for="(item,i) in model.restrained" :key="i" :span="12">
                <el-card>
                  <el-tag>{{i+1}}</el-tag>
                  <el-form-item label="" label-width="80px">
                    <el-select v-model="item.hero" filterable placeholder="">
                      <el-option
                        v-for="item in heroList"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="" label-width="80px">
                    <el-input type="textarea" v-model="item.desc"></el-input>
                  </el-form-item>
                  <el-button type="danger" @click="delRestrained(i)"></el-button>
                </el-card>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane label="">
            <el-button type="primary" size="small" @click="model.restraint.push({})">+ </el-button>
            <el-row :gutter="20" type="flex" style="flex-wrap: wrap">
              <el-col v-for="(item,i) in model.restraint" :key="i" :span="12">
                <el-card>
                  <el-tag>{{i+1}}</el-tag>
                  <el-form-item label="" label-width="80px">
                    <el-select v-model="item.hero" filterable placeholder="">
                      <el-option
                        v-for="item in heroList"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="" label-width="80px">
                    <el-input type="textarea" v-model="item.desc"></el-input>
                  </el-form-item>
                  <el-button type="danger" @click="delRestraint(i)"></el-button>
                </el-card>
              </el-col>
            </el-row>
          </el-tab-pane>
        </el-tabs>
      </el-form>
      <el-button class="save-btn" type="primary" @click="save"></el-button>
    </el-card>
  </div>
</template>
 
<script>
// 
import { createHero, getHero, updateHero, getHeroList } from 'network/admin/hero'
import { getCateList } from 'network/admin/category'
import { getItemList } from 'network/admin/item'

import { uploadMixin } from '../../common/mixins'

import Breadcrumb from '../../components/commom/Breadcrumb'

export default {
  name: "HeroEdit",
  props: {
    id: {
      type: String,
      default: ''
    }
  },
  components: {
    Breadcrumb
  },
  mixins: [uploadMixin],
  data() {
    return {
      value: [],
      //
      model: {
        avatar: '',
        scores: {},
        skills: [],
        downWind: {},
        upWind: {},
        partners: [],
        restrained: [],
        restraint: []
      },
      heroCateList: null,
      itemList: [],
      heroList: []
    }
  },
  methods: {
    // 
    async save() {
      if (!this.id) {
        const res = await createHero(this.model)
        if(!res) return
        this.$message.success('')
        this.$router.push('/hero/list')
      } else {
        const res = await updateHero(this.id, this.model)
        if(!res) return
        this.$message.success('')
        this.$router.push('/hero/list')
      }
    },
    //id
    async getHero() {
      const res = await getHero(this.id)
      this.model = res.data
    },
    async edit(row) {
      const res = await getHero(row._id)
      // console.log(res.data);
      this.$router.push(`/hero/edit/${row._id}`)
    },
    // 
    async getCateList() {
      const res = await getCateList()
      for (let item of res.data) {
        if (item.name === '') {
          this.heroCateList = item.children
          break
        }
      }
    },
    //
    async getItemList() {
      const res = await getItemList()
      this.itemList = res.data
    },
    //
    addSkills() {
      this.model.skills.push({})
    },
    deleteSkill(i) {
      this.model.skills.splice(i, 1)
    },
    // 
    async getHeroList() {
      const res = await getHeroList()
      if (!res) return
      this.heroList = res.data
    },
    bannerHanderler(res) {
      this.$set(this.model, 'banner', res.url)
    },
    delPartner(i) {
      this.model.partners.splice(i, 1)
    },
    delRestrained(i) {
      this.model.restrained.splice(i, 1)
    },
    delRestraint(i) {
      // console.log(i);
      this.model.restraint.splice(i, 1)
    }
  },
  created() {
    this.id && this.getHero()
    this.getCateList()
    this.getItemList()
    this.getHeroList()
  }
}
</script>
 
<style scoped>
.save-btn {
  position: absolute;
  top: 15px;
  right: 35px;
}
.el-tag {
  margin: 10px;
}
.skill-card {
  max-width: 40%;
}
.banner {
  width: 300px;
  vertical-align: middle;
}
</style>