import { GUID_req } from '../request'

//
function createCate(data) {
  return GUID_req({
    url: '/categories',
    method: 'post',
    data
  })
}

//id()
function getCate(cateId) {
  return GUID_req({
    url: `/categories/${cateId}`
  })
}

//
function updateCate(cateId, data) {
  return GUID_req({
    url: `/categories/${cateId}`,
    method: 'put',
    data
  })
}

//
function deleteCate(cateId) {
  return GUID_req({
    url: `/categories/${cateId}`,
    method: 'delete'
  })
}

//
function getCateList() {
  return GUID_req({
    url: '/categories'
  })
}

export {
  createCate,
  getCate,
  updateCate,
  deleteCate,
  getCateList
}