<template>
  <div>
    <breadcrumb text="" />
    <el-card>
      <el-button type="primary" icon="el-icon-plus" @click="addArticle"></el-button>
      <el-table :data="articleList">
        <el-table-column type="index" label=""></el-table-column>
        <el-table-column label="" border stripe>
          <template slot-scope="scope">
            <span>{{scope.row.cate.map(item => item.name).join('/')}}</span>
          </template>
        </el-table-column>
        <el-table-column width="550px" label="" prop="title"></el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small" type="primary" icon="el-icon-edit" @click="edit(scope.row)"></el-button>
            <el-button size="small" type="danger" icon="el-icon-delete" @click="_delete(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageParams.pagenum"
        :page-sizes="[5, 8, 10, 15]"
        :page-size="pageParams.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>
 
<script>

import Breadcrumb from '../../components/commom/Breadcrumb'
//
import { deleteArticle, getArticleList } from 'network/admin/article'

export default {
  name: "ArticleList",
  data() {
    return {
      //
      articleList: [],
      pageParams: {
        pagenum: 1,
        pagesize: 5
      },
      total: 0
    }
  },
  components: {
    Breadcrumb
  },
  methods: {
    //
    async getArticleList() {
      const res = await getArticleList(this.pageParams)
      this.total = res.data.total
      this.articleList = res.data.data
    },
    //
    edit(row) {
      this.$router.push(`/article/edit/${row._id}`)
    },
    //
    async _delete(row) {
      try {

        await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })

        const res = await deleteArticle(row._id)
        if(!res) return
        this.$message.success('')
        this.getArticleList()

      } catch (error) {
        this.$message.info('')
      }

    },
    // 
    addArticle() {
      this.$router.push('/article/create')
    },
    handleSizeChange(pagesize) {
      this.pageParams.pagesize = pagesize
      this.getArticleList()
    },
    handleCurrentChange(pagenum) {
      this.pageParams.pagenum = pagenum
      this.getArticleList()
    }
  },
  async created() {
    this.getArticleList()
    const res = await getArticleList()
    // console.log(res.data);
  }
}
</script>
 
