import { GUID_req, upload } from '../request'

//
function createItem(data) {
  return GUID_req({
    url: '/items',
    method: 'post',
    data
  })
}

//id()
function getItem(itemId) {
  return GUID_req({
    url: `/items/${itemId}`
  })
}

//
function updateItem(itemId, data) {
  return GUID_req({
    url: `/items/${itemId}`,
    method: 'put',
    data
  })
}

//
function deleteItem(itemId) {
  return GUID_req({
    url: `/items/${itemId}`,
    method: 'delete'
  })
}

//
function getItemList(params) {
  return GUID_req({
    url: '/items',
    params
  })
}
export {
  createItem,
  getItem,
  updateItem,
  deleteItem,
  getItemList
}