<template>
  <div class="login">
    <!--  -->
    <el-card class="login-card">
      <el-form
        label-width="0"
        ref="loginFormRef"
        :rules="LoginRules"
        :model="LoginForm"
        class="login-form"
      >
        <h3 class="title"></h3>

        <el-form-item class="username" prop="username">
          <!--  -->
          <el-input
            @keyup.native.enter="login"
            v-model="LoginForm.username"
            prefix-icon="el-icon-user-solid"
            placeholder=""
          ></el-input>
        </el-form-item>

        <el-form-item class="password" prop="password">
          <!--  -->
          <el-input
            @keyup.native.enter="login"
            v-model="LoginForm.password"
            prefix-icon="el-icon-lock"
            type="password"
            placeholder=""
          ></el-input>
        </el-form-item>

        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
 
<script>
//
import { loginReq } from 'network/admin/login'

export default {
  name: 'Login',
  data() {
    let checkUsername = (rule, value, callback) => {
      const regUsername = /^[a-zA-Z][\w]{2,9}$/
      if (regUsername.test(value)) return callback()
      callback(new Error(''))
    }

    let checkPassword = (rule, value, callback) => {
      const regPsw = /^[\w.]{6,15}$/
      if (regPsw.test(value)) return callback()
      callback(new Error(''))
    }

    return {
      //
      LoginForm: {
        username: 'test',
        password: '123456'
      },
      //
      LoginRules: {
        username: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            validator: checkUsername,
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            validator: checkPassword,
            trigger: 'blur'
          }
        ],
      }
    }
  },
  methods: {
    //,
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields()
    },
    login() {

      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')

        const res = await loginReq(this.LoginForm)
        if (!res) return

        this.$message.success('')
        // token
        sessionStorage.setItem('token', res.data.token)
        // main
        this.$router.push('/')

      })

    }
  }
}
</script>
 
<style  scoped>
.title {
  width: 100%;
  text-align: center;
}
.login {
  width: 100%;
  height: 100%;
  background: url("../assets/img/login/login-bg.jpg") no-repeat 0 0;
  background-size: cover;
}
.login-card {
  position: absolute;
  left: 50%;
  top: 50%;
  padding: 30px;
  transform: translate(-50%, -50%);
  width: 25%;
  height: 30%;
  min-width: 400px;
  min-height: 230px;
}
.btns {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 0;
}
.login-form {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 75%;
}
.username,
.password {
  margin-bottom: 30px;
}
</style>