<template>
  <div>
    <breadcrumb text="" />
    <el-card>
      <el-button type="primary" icon="el-icon-plus" @click="addItemClick"></el-button>
      <!--  -->
      <el-table :data="itemList" border stripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <div>{{scope.row.desc}}</div>
            <div class="item-detail">{{scope.row.detail}}</div>
          </template>
        </el-table-column>
        <el-table-column type="index" label=""></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <img class="item-icon" :src="scope.row.icon" />
          </template>
        </el-table-column>
        <el-table-column label="" prop="star"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="edit(scope.row)"></el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="_delete(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageParams.pagenum"
        :page-sizes="[5, 8, 10, 15]"
        :page-size="pageParams.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      :title="!this.formData._id?'':''"
      :visible.sync="dialogVisible"
      width="50%"
      @closed="dialogClose"
    >
      <el-form label-width="80px">
        <!--  -->
        <el-form-item label="">
          <el-input v-model="formData.name"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="">
          <el-upload
            class="avatar-uploader"
            :action="action+'item'"
            :headers="token"
            :show-file-list="false"
            :on-success="res=>$set(formData,'icon',res.url)"
          >
            <img v-if="formData.icon" :src="formData.icon" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <el-rate show-score :max="3" v-model="formData.star"></el-rate>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.desc"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.detail"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="ok"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
 
<script>
//
import axios from 'axios'

import { getItemList, createItem, getItem, updateItem, deleteItem } from 'network/admin/item'

import { uploadMixin } from '../../common/mixins'

import Breadcrumb from '../../components/commom/Breadcrumb'

export default {
  name: "ItemList",
  mixins: [uploadMixin],
  data() {
    return {
      //
      itemList: [],
      dialogVisible: false,
      // 
      formData: {},
      id: '',
      // 
      pageParams: {
        pagenum: 1,
        pagesize: 5
      },
      total: 0
    }
  },
  components: {
    Breadcrumb
  },
  methods: {
    //
    async getItemList() {
      const res = await getItemList(this.pageParams)
      this.total = res.data.total
      this.itemList = res.data.data
    },
    //
    async edit(row) {
      this.id = row._id
      const res = await getItem(row._id)
      if (!res) return
      res.data.star = Number(res.data.star)
      this.formData = res.data
      this.dialogVisible = true
    },
    //
    async _delete(row) {
      try {

        await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })

        const res = await deleteItem(row._id)
        if (!res) return 
        this.$message.success(res.data.message)
        this.getItemList()

      } catch (error) {
        this.$message.info('')
      }

    },
    //
    async addItemClick() {
      this.dialogVisible = true
    },
    async ok() {

      if (!this.formData._id) {
        const res = await createItem(this.formData)
        if (!res) return this.$message.error('')
        this.$message.success('')
        this.getItemList()
        this.dialogVisible = false
      } else {
        const res = await updateItem(this.id, this.formData)
        if (!res) return this.$message.error('')
        this.$message.success('')
        this.getItemList()
        this.dialogVisible = false
      }
    },
    dialogClose() {
      this.formData = {}
    },
    handleSizeChange(pagesize) {
      this.pageParams.pagesize = pagesize
      this.getItemList()
    },
    handleCurrentChange(pagenum) {
      this.pageParams.pagenum = pagenum
      this.getItemList()
    }
  },
  created() {
    this.getItemList()
  }
}
</script>
 
<style scoped>
.item-icon {
  width: 45px;
  height: 45px;
}
.item-detail {
  padding-top: 10px;
}
</style>