import { GUID_req, upload } from '../request'

//
function createArticle(data) {
  return GUID_req({
    url: '/articles',
    method: 'post',
    data
  })
}

//id()
function getArticle(itemId) {
  return GUID_req({
    url: `/articles/${itemId}`
  })
}

//
function updateArticle(itemId, data) {
  return GUID_req({
    url: `/articles/${itemId}`,
    method: 'put',
    data
  })
}

//
function deleteArticle(itemId) {
  return GUID_req({
    url: `/articles/${itemId}`,
    method: 'delete'
  })
}

//
function getArticleList(params) {
  return GUID_req({
    url: '/articles',
    params
  })
}

//
function articleImgUpload(data) {
  return upload({
    url: '/article',
    method: 'post',
    data
  })
}

export {
  createArticle,
  getArticle,
  updateArticle,
  deleteArticle,
  getArticleList,
  articleImgUpload
}