<template>
  <div>
    <breadcrumb text="" />
    <el-card>
      <el-button type="primary" icon="el-icon-plus" @click="addCate"></el-button>
      <tree-table
        :data="cateList"
        show-index
        index-text=""
        :columns="columns"
        :expand-type="false"
        :selection-type="false"
        border
      >
        <template slot="operate" slot-scope="scope">
          <el-button size="mini" type="primary" icon="el-icon-edit" @click="editCateClick(scope.row)"></el-button>
          <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteCateClick(scope.row)"></el-button>
        </template>
      </tree-table>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @closed="addDialogClose">
      <el-form label-width="80px">
        <el-form-item label="">
          <el-input maxlength="10" v-model.trim="newModel.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input maxlength="20" v-model.trim="newModel.desc"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            :props="cateProps"
            :options="cateList"
            v-model="newModel.parent"
            clearable
            @change="handleChange"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="okAdd"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form label-width="70px">
        <el-form-item label="">
          <el-input v-model="model.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input maxlength="20" v-model.trim="model.desc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="okEdit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
 
<script>
// 
import { createCate, getCate, updateCate, getCateList, deleteCate } from 'network/admin/category'

import Breadcrumb from '../../components/commom/Breadcrumb'

export default {
  name: "CategoryEdit",
  components: {
    Breadcrumb
  },
  data() {
    return {
      newModel: {},
      model: {},
      //
      cateList: [],
      cateProps: {
        expandTrigger: 'hover',
        value: '_id',
        label: 'name',
        children: 'children',
        emitPath: false,
        checkStrictly: true
      },
      columns: [
        {
          label: '',
          prop: 'name',
          align: 'center',
          headerAlign: 'center'
        },
        {
          label: '',
          type: 'template',
          template: 'operate',
          align: 'center',
          headerAlign: 'center'
        }
      ],
      id: '',
      editDialogVisible: false,
      addDialogVisible: false
    }
  },
  methods: {
    //
    async getCateList() {
      const res = await getCateList()
      const data = res.data
      // 
      for (let item1 of data) {
        // 
        if (item1.children && item1.children.length === 0) {
          item1.children = null
          continue
        }

        for (let item2 of item1.children) {
          // 
          if (item2.children && item2.children.length === 0) {
            item2.children = null
            continue
          }

          for (let item3 of item2.children) {
            // 
            item3.children = null
            item3.disabled = true
          }
        }
      }

      // console.log(data);

      this.cateList = data

    },
    handleChange() {
      // console.log(this.model.parent);
    },
    //
    addCate() {
      this.addDialogVisible = true
    },
    async okAdd() {
      const res = await createCate(this.newModel)
      if (!res) return 
      this.$message.success('')
      this.getCateList()
      this.addDialogVisible = false
    },
    addDialogClose() {
      this.newModel = {}
    },
    //
    async editCateClick(row) {
      this.id = row._id
      const res = await getCate(row._id)
      // console.log(res.data);
      this.model = res.data
      this.editDialogVisible = true
    },
    async okEdit() {
      const res = await updateCate(this.id, this.model)
      if (!res) return 
      this.$message.success('')
      this.getCateList()
      this.editDialogVisible = false
    },
    //
    async deleteCateClick(row) {
      try {
        await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })

        if (row.children && row.children.length > 0) return this.$message.error('')

        const res = await deleteCate(row._id)
        if (!res) return 
        this.$message.success('')
        this.getCateList()
      } catch (error) {
        this.$message.info('')
      }
    }
  },
  created() {
    this.getCateList()
  }
}
</script>
 
<style scoped>
</style>